.heading {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin: 3rem 0 3rem 0;

}

.portfolio  {
  height: 70vh;
  overflow-y: scroll;

}




/* heading */
.portfolio span:nth-of-type(1) {
  color: var(--black);
  font-size: 1rem;
  font-weight: bold;

}

.portfolio span:nth-of-type(2) {
  color: var(--orange);
  font-size: 2.5rem;
  font-weight: bold;
  
}


