.intro {
  display: flex;
 background: var(--gradient-bg);
 height: auto;
 padding: 1rem;
}

/* left side styling */
.i-left {
  padding-top: 1.5rem;
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column; 
  gap: 2rem;
  flex-wrap: wrap;
}
.i-name {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
/* .i-name>:nth-child(1) {
  color: var(--black);
  font-weight: bold;
  font-size: 3rem;
} */
 .i-name >:nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 3rem;
}
.i-name > :nth-child(3), .more {
  font-weight: 100;
  font-size: 14px;
  color: var(--gray);
  margin-top: 10px;
}



.more {
  display: block;
}


.i-icons {
  margin-top: 3rem;
  display: flex;
  gap: 0rem;
}
.i-icons > * {
  transform: scale(0.5);
}
.i-icons > *:hover {
  cursor: pointer;
} 


/* right side styling */
.i-right  {
  display: flex;
  flex: 1;
  position: relative;
  justify-content: center;
  align-items: center;
  

}
.i-right > img {
  object-fit: contain;
  width: 28rem;
  height: auto;

}






@media screen and (max-width: 950px) {

    .intro {
      display: flex;
      /* margin-bottom: 4rem; */
    }
  
    /* left side styling */
    .i-left {
      display: flex;
      position: relative;
      flex: 1;
      flex-direction: column;
      gap: 2rem;
      flex-wrap: wrap;
    }
  
    .i-name {
      display: flex;
      flex-direction: column;
      gap: 0.20rem;
    }
  
    .i-name>:nth-child(1) {
      color: var(--black);
      font-weight: bold;
      font-size: 1.5rem;
    }
  
    .i-name>:nth-child(2) {
      color: var(--orange);
      font-weight: bold;
      font-size: 1.5rem;
    }
  
    .i-name> :nth-child(3) {
      font-weight: 100;
      font-size: 12px;
      color: var(--gray);
      margin-top: 10px;
    }
  
    .i-icons {
      margin-top: 3rem;
      display: flex;
      /* display: none; */
    }
  
    .i-icons>* {
      transform: scale(0.5);
    }

        /* right side styling */
        .i-right {
          flex: 1;
          position: relative;
          transform: scale(0.7);

        }
    
       

 
}
@media screen and (max-width: 670px) {
  .i-right {
    flex: 1;
    position: relative;
    transform: scale(0.5);

  }

  .i-left {
      gap: 0.2;    
  }

  .i-icons {
    margin-top: 0;
   
  }

  .i-icons>* {
    transform: scale(0.3);
  }

  .i-right > img {
    width: 20rem;
  
  }
  


}


/* @media screen and (max-width: 380px) {
  .intro {
    transform: scale(0.6);

  }
} */



@media screen and (max-width: 550px) {
  .intro {
    flex-direction: column;
    gap: 1rem;
  }

  .i-right {
    transform: scale(0.7);
    /* left: -3rem; */
  }

  .i-name>:nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
  }

  .i-name>:nth-child(2) {
    color: var(--orange);
    font-weight: bold;
    font-size: 2rem;
  }

button {
  margin: 0;
}

}