.card2 > img {
  border-radius: 20px ;
  width: 100%;
  
} 

.card2 {
  width: 30rem;
  height: 17rem;
  display: flex;
  position: relative;
  padding: 0.2rem;
  margin: 2rem ;
  border: 2px solid var(--orange);
  border-radius: 20px;
  object-fit: contain;
}



.cbutton {
    border: none;
    padding: 10px;
    font-size: 10px;
    position: absolute;
    top: 0;
    left: 0;
    margin: 1rem;
    
}

.cbutton:hover{
    transform: scale(1.1);
    cursor: pointer;
}
/* cards */



@media screen and (max-width: 1030px){
  .card2{
    width: 23rem;
    height: 15rem;
    margin: 1.8rem;
  }
}
@media screen and (max-width: 950px){
  .card2{
    width: 21rem;
    height: 14rem;
    margin: 1.2rem;
  }
}




@media screen and (max-width: 900px){
  .card2{
    width: 19rem;
    height: 11rem;
    margin: 1rem;
  }
}

@media screen and (max-width: 770px){
  .card2{
    width: 18rem;
    height: 10.3rem;
  }
}




@media screen and (max-width: 580px){
  .card2{
    width: auto;
    height: auto;
  }
}