.BnB__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3rem;
    padding: 0.5rem;
    background: (--gradient-inverse-bg);
}

.nav_list {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    list-style: none;
}

.nav_list li a {
    text-decoration: none;

}

.left_side{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
}

.right_side {
    margin-right: 2rem;
}

.BnB__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.n-name {
    margin: 0 2rem;
}

.BnB__navbar-links_logo {
    
}

.BnB__navbar-links_logo img {
    width: 62.56px;
    height: 36.02px;
}

.BnB__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.BnB__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.BnB__navbar-links_container p,
.BnB__navbar-sign p,
.BnB__navbar-menu_container p {
    color: #070707;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.BnB__navbar-sign button,
.BnB__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.BnB__navbar-menu {
    margin-left: 1rem;
    /* background: #070707; */
    color: #141414;
    display: none;
    position: relative;
}

.BnB__navbar-menu svg {
    cursor: pointer;
}

.BnB__navbar-menu_container {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FF4820;
    background: #f5f5f5;
    border: 2px solid #FF4820;
    position: absolute;
    height: 60vh;
    top: 3rem;
    left: -60vw;
    width: 20rem;
    border-radius: 1rem 0 1rem;
    z-index: 5;
    opacity: 0.9;
}

.mobileNav_list li{
    font-size: 2rem;
    list-style-type: none;
    padding: 1rem;
}

.BnB__navbar-menu_container p {
    margin: 1rem 0;
}

.BnB__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 900px) {

    .BnB__navbar-menu_container {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #FF4820;
        background: #f5f5f5;
        border: 2px solid #FF4820;
        position: absolute;
        height: 60vh;
        top: 3rem;
        left: -80vw;
        width: 45rem;
        border-radius: 1rem 0 1rem;
        z-index: 5;
        opacity: 0.9;
    }

    .mobileNav_list li{
        font-size: 2.5rem;
        padding: 2rem;
    }

    .BnB__navbar-links_container {
        display: none;
    }

    .BnB__navbar {
        gap: 1rem;
        padding: 0.1rem;
    }

    .BnB__navbar-menu {
        display: flex;
    }

   
}

@media screen and (max-width: 700px) {
    .BnB__navbar {
        padding: 2rem 4rem;
    }

    
}

@media screen and (max-width: 550px) {
    .BnB__navbar {
        padding: 0.5rem;
        /* transform: scale(0.8); */
    }

    .BnB__navbar-sign {
        display: none;
    }

    .BnB__navbar-menu_container {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #FF4820;
        background: #f5f5f5;
        border: 2px solid #FF4820;
        position: absolute;
        height: 60vh;
        top: 3rem;
        left: -60vw;
        width: 20rem;
        border-radius: 1rem 0 1rem;
        z-index: 5;
        opacity: 0.9;
    }

   

    .BnB__navbar-menu_container-links-sign {
        display: block;
    }

    .mobileNav_list li{
        font-size: 1.5rem;
        padding: 1rem;
    }
}


@media screen and (max-width: 280px) {

    .n-name {
        display: none;
    }

}