.services {
  padding: 3rem 0.2rem 3rem 0.2rem;
  background: var(--gradient-bg);
  color: "var(--gradient-bg)";
  height: auto;
  overflow-y: scroll;
}


.info {
  display: flex;
  color: #555;
}

.info_card {
  flex: 1;
}

/* right sided */


.header {
  color: #850309;
}

.top_header {
  text-align: center;
}

.top_header h3 {
  color: black;
  margin: 1rem;
}


.webdesign {
  position: relative;
  border: 1px solid rgb(104, 68, 2);
  border-radius: 5px;
  padding: 0.3rem;
  margin: 1rem 1rem;
  text-align: justify;
}

.title {
  padding: 1rem;
  font-size: 1.2rem;
  font-weight: 900;
  display: flex;
  flex-wrap: wrap;
  
}





@media screen and (max-width: 980px) {
  .services {

      
      flex-direction: column;
      gap: 5rem;
      height: auto;
      
    }

    .info {
      flex-direction: column;
      
    }
  
 

}



@media screen and (max-width: 580px) {
  .services {
    
    flex-direction: column;
    gap: 5rem;
    height: 66rem;
   
  }

  .info {
    flex-direction: column;
    
  }

 
}
